.shap{
    font-weight: bolder;
    background-color:white;
    height: 200px;
    width: 200px;
    margin: 10px;
    display: inline-block;
   line-height: 10;
   font-style: oblique;
  font-size: 20px;

   &:hover{
     border: 5px solid ges(sol);
     transition: 1s;
   }
   @include lay(){
    display: block;
    position: relative;
    left: 30%;
   }
 }
 .blas{
    background:linear-gradient(100deg,rgb(197, 193, 193), rgb(89, 87, 87));
    height: 100vh;
    @include lay(){
       height: 170vh;
    }
    @include hok(){
        height: 220vh;
    }
 }

 @mixin hop() {
    position: absolute;
    font-size: 60px; 
    color: rgb(92, 91, 91);
    left: 60%
}
    
 .pash{
    list-style-type: none;
    position: absolute;
    left: 23%;
    @include lay(){
        left: 45%;
    }
    @include hok(){
        top: 60%;
        left: 15%;
    }
    @include vop(){
        left: -10%;
    }

 
  }
  .out{
    position: absolute;
    top: 92%;
    left: 55%;
    height: 30px;
    width: 250px;
    font-size: 17px;
    border-radius: 10px;
    border-top: none;
    border-left: none;
    border-bottom: none;
    border-right: none;
    font-weight: bolder;
    &:hover{
        background-color: rgb(105, 103, 103);
    }
  }