.samo{
    height: 430px;
    width: 330px;
   background-color: rgb(92, 91, 91);
   position: absolute;
   left: 20%;
   top: 20%;
   border-radius: 10px;
   box-shadow: -3px -3px  3px gray;
  
  }
  .samu{
    @extend .samo;
    position: absolute;
    left: 50%;
    z-index: 2;
    @include jop(){
      left: 40%;
      top:25% ;
     }
     @include hok(){
      left: 15%;
     }
     @include vop(){
      left: 5%;  
      width: 320px;   
    }
  };
  @mixin com {
    left: 10%;
    color: white;
    font-weight: 700;
    
  }
  @mixin moc() {
    left: 30px;
    width: 250px;
    height: 30px;
    border-top: none;
    border-left: none;
    border-right: none;
    background-color: ges(sol);
    border-radius: 5px;
    padding-left: 10px;
    color: black;
    
  }
  .reg{
    position: absolute;
    top: 30%;
    @include com();
  }
  .wod{
    position: absolute;
    top: 48%;
    @include com();
   
  }
  .tex{
    position: absolute;
    top: 38%;
    @include moc();
  }
  
  .as{
    position: absolute;
    top: 56%;
    @include moc();
  }
  .gu{
    position: absolute;
    top: 70%;
    left: 10%;
    height: 33px;
    width: 250px;
    font-size: 15px;
    font-weight: 700;
    border-radius: 10px;
    &:hover{
        background-color: rgb(92, 91, 91);
        color: white;
        transition: 1s;
        
    }
  }
  .fog{
    position: absolute;
    top: 74%;
    left: 20%;
    font-size: 13px;
    color: black;
    font-weight: 2px;
    @include gru(){
      top: 76%;
    }
  }
  .den{
    font-size: 20px;
    color: white;
    font-weight: 900;
  }
  .cov{
    background-color: black;
    height: 50px;
    width: 320px;
    position: absolute;
    top: -1px;
    border-bottom-left-radius: 100px;
    border-bottom-right-radius: 100px;
    line-height: 2;
    border-radius: 10px;
  }
  .boss{
    background: linear-gradient(100deg, rgb(197, 193, 193), rgb(89, 87, 87));
    height: 100vh;
  }
  .loy{
    position: absolute;
    top: 20%;
    left: 7%;
    font-size: 30px;
    font-weight: bolder;
    font-style: italic;
    font-family: 'Times New Roman', Times, serif;
    @include jop(){
      top: 12%;
    }

  }
  .set{
    height:400px;
    width: 400px;
    margin-left: -50%;
    top: 25%;
    z-index: 1;
    @include jop(){
      height: 200px;
      width: 200px;
      left: 5%;
      top: 45%;
    }
    @include hok(){
      height: 70px;
      width: 70px;
      top: 10%;
      left: 58%;
     }
     @include vop(){
      left:70%;
     }
  }
  .big{
    font-size: 35px;
    position: absolute;
    left: 78%;
    top: 38%;
    z-index: 2;
    color: rgb(110, 109, 109);
  
  }
  .word{
    font-size: 35px;
    color: rgb(110, 109, 109);
    position: absolute;
    left: 77%;
    top: 55%;
    z-index: 2;
  }
  .lig{
    font-size: 30px;
    color: rgb(56, 55, 55);
    position: absolute;
    top: 70.3%;
    left: 55%;
    z-index: 2;
  }
  .cler{
    position: absolute;
    top: 14.3%;
    left: 41%;
    z-index: 2;
    font-size: 45px;
    color: rgb(199, 185, 185);
  }
  .yop{
    position: absolute;
    top: 115%;
    left: 20%;
    width: 350px;
  }
  .you{
    position: absolute;
    top: 115%;
    left: 60%;
    height: 450px;
    width: 450px;
    z-index: 1;
  }