.fom{
    background: linear-gradient(100deg,#A9A9A9, #6a6969);
    height: 300px;
    width: 300px;
    position: absolute;
    top: 20%;
    left: 45%;
    width: 600px;
    height: 400px;
    border-radius: 10px;
    @include gru(){
      left: 30%;
    }
    @include nit(){
      left: 10%;
      top: 25%;
    }
    @include jop(){
      left:2% ;
    }
    @include hok(){
      width: 300px;
      height: 700px;
      left: 20%;
    }
    @include vop(){
      left: 8%;
    }

  }
  @mixin pop() {
    position: absolute;
    left: 10%;
  }
  @mixin sak() {
    top: 12%;
  }
  @mixin exp() {
    height: 33px;
    width: 230px;
    border-radius: 10px;
    border-right: none;
    border-left: none;
    border-bottom: none;
    border-top: none;
    padding-left: 10px;
    
    
  }
  @mixin tex() {
    font-weight: bolder;
    
  }

  @mixin kas() {
    position: absolute;
    left: 55%;
  }
  .time{
    @include pop();
    top: 4%;
    @include tex();
    
  }
  .nam{
    @include pop();
   @include sak();
   @include exp();
   @include hok(){
    top: 9%;
   }
  }
  .las{
   @include kas();
    top: 4%;
    @include tex();
    @include hok(){
      top: 15%;
      left: 10%
    }
   
  }
  .asi{
    @include exp();
    @include sak();
   @include kas();
   @include hok(){
    top: 20%;
    left: 10%
   }
  }
  .acad{
   @include pop();
    top: 24%;
    @include tex();
    @include hok(){
      top: 26%;
      left: 10%
    }
  }
  .com{
    @include pop();
    top: 33%;
    @include exp();
    @include hok(){
      top: 31%;
     }
  }
  .sem{
   @include kas();
   top: 24%;
   @include tex();
   @include hok(){
    top: 37%;
    left: 10%
  }
  }
  .siw{
    position: absolute;
    top: 33%;
    left:55% ;
    @include exp();
    @include hok(){
      top: 42%;
      left: 10%;
     }
  }
  .gen{
    position: absolute;
    top: 43%;
    left: 10%;
    @include tex();
    @include hok(){
    top: 48%;
    } 
  }
  .neg{
    position: absolute;
    left: 10%;
    top: 52%;
    @include exp();
    @include hok(){
      top: 53%;
      left: 10%;
     }
  }
  .coy{
    position: absolute;
    left:55% ;
    top:52% ;
    @include exp();
    @include hok(){
      top: 64%;
      left: 10%;
     }
  }
  .cou{
    position: absolute;
    left:55% ;
    top: 43%;
    @include tex();
    @include hok(){
      top: 59%;
      left: 10%;
      } 
  }
  .lev{
    position: absolute;
    left: 10%;
    top: 63%;
    @include tex();
    @include hok(){
      top: 70%;
      left: 10%;
      } 
  }
  .vel{
    position: absolute;
    left: 10%;
    top:72% ;
    @include exp();
    @include hok(){
      top: 75%;
      left: 10%;
     }

  }
  .bus{
    position: absolute;
    height: 50px;
    width: 230px;
    left:55% ;
    top: 67%;
    border-radius: 10px;
    border-right: none;
    border-left: none;
    border-bottom: none;
    border-top: none;
    @include hok(){
      top: 86%;
      left: 10%;
      } 
    &:hover{
      background-color: #2d2c2c;
      color: white;
      transition: 1s;
    }
  }
  .saf{
    height: 100vh;
    background: linear-gradient(100deg,#f6eeee, #c5c3c3);
    @include hok(){
     height: 130vh;
     }
    
  }
  .sign{
    position: absolute;
    top: 15%;
    left: 8%;
    font-size: 30px;
    font-style: oblique;
    @include gru(){
      left: 10%;
    }
    @include nit(){
      left: 12%;
    }
    @include jop(){
      left: 14%;
    }
    @include vop(){
      left: 20%;
    }

  }
 
  .mag{
    height: 500px;
    width: 500px;
    position: absolute;
    left: 5%;
    top: 20%;
    @include gru(){
      left: 2%;
      height: 400px;
      width: 300px;
    }
    
  }
  .cut{
    position: absolute;
    top: 14.5%;
    left: 3.47%;
    font-size: 50px;
    color: #2d2c2c;
  }

  .va{
    position: absolute;
    top: 115%;
    left: 15%;
  }
  .vi{
    position: absolute;
    top: 117%;
    left: 70%;
  }
  .ve{
    position: absolute;
    top: 113%;
    left: 40%;
  }