
@mixin item {
    position: absolute;
    left: 55%;
    @include jop(){
        top: 60%;
    }
    @include hok(){
        left: 30%;
    }
}

.pof{
    @include item();
}

.res{
    @include item();
}
.tid{
    height: 300px;
    width: 300px;
    position: absolute;
    left: -25%;
}
.onl{
    @include item();
}
.tran{
    @include item(); 
}
.rec{
    @include item();  
}
.lib{
    @include item();  
}
.set{
    @include item();
}